.home {
    img{
        cursor: pointer;
    }
    .gallery-headline {
        margin-bottom: 2rem;
        font-size: 3rem;
        font-weight: 500;
    }
    .gallery {
        display: grid;
        margin-bottom: 5rem;
        grid-template-columns: repeat(3, minmax(8rem, 1fr));
        gap: 16px;
        width: 100%;

        .card {
            box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px !important;
            border-radius: 16px !important;

            .img-container {
                border-radius: 16px 16px 0 0;

                @include aspect-ratio(1, 1);
            }
            .descrption {
                padding: 1rem;
            }
        }
    }
    .cheap {
        color: rgb(0, 0, 0);
        box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px !important;
        padding-bottom: 2rem;
    }
    .filters {
        margin-bottom: 2rem;
        box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px !important;
        .budget,select{
            border-radius: 99px;
            border: none;
            outline: none;
            background-color: inherit;
            box-shadow: 0 1px 6px 0 rgba(32, 33, 36, .28);
            // padding: .2rem;   
             }
             select{
                 width: 5rem;
             }
     
        form {
            box-shadow: 0px 1px 3px 0px rgba(179, 179, 179, 1);
            margin-bottom: 1rem;
            input {
                padding: 1rem 0rem;
                border: none;
                background-color: inherit;
                border-right: 0.6px solid rgb(185, 181, 181);
                margin-right: 2rem;
            }
            .go{
                background-color:#0075ff;
                color: white;
                font-size: 1.2rem;
                border: none;
                width: 4rem;
                cursor: pointer;
            
            }

            .Travellers {
                flex-grow: 1;
                display: flex;
                align-items: center;
                input {
                    border: none;
                }
            }
        }
        .parameters{

            input{
                    width: 4rem !important;
                }
            
        }
        h2 {
            margin-bottom: 0.7rem;
        }
        h3 {
            line-height: 2rem;
        }
    }
    .collage {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 1rem;
        .first {
            grid-area: 1 / 1 / 3 / 2;
            .img-container {
                height: 100%;
                border-radius: 16px;
                @include aspect-ratio(1, 1);
            }
        }

        .second {
            grid-area: 1/ 2 / 2 / 3;
            .img-container {
                border-radius: 16px;

                @include aspect-ratio(2, 1);
            }
        }
        .third {
            grid-area: 2/ 2 / 3 / 3;
            .img-container {
                border-radius: 16px;

                @include aspect-ratio(2, 1);
            }
        }
    }
    .subscribe {
        margin: 5rem 0;
        box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
        border-radius: 16px;
        position: relative;

        .img-container {
            width: 15rem;

            img {
                width: 100%;
                border-radius: 16px 0 0 16px ;

            }
        }
        .offer{
            padding: 1rem;
            h2{
                line-height: 2.9rem;
            }
            p{
                max-width:40ch;
                font-size: 1.2rem;
            }
            form{
                position: absolute;
                bottom:1rem ;
                right: 1rem;
                button{
                    background-color: inherit;
                    border: none;
                    cursor: pointer;
                    font-weight: 600;
                }
                input{
                    border-radius: 99px;
                    border: none;
                    outline: none;
                    box-shadow: 0 1px 6px 0 rgba(32, 33, 36, .28);
                    padding: .5rem;
                }
            }
        }
    }
}
