.main-Header{
    color: #000000;
    margin-bottom: 2rem;
    background-color: rgb(252, 252, 252);
    z-index: 3;
    input{
        border-radius: 99px;
        border: none;
        outline: none;
        box-shadow: 0 1px 6px 0 rgba(32, 33, 36, .28);
        width: 33%;
        padding: .5rem;
    }
    .logo-container{
        width: 6rem;
        margin-right:0 ;
        img{
            width: 100%;
        }
    }
    ul{
        li:not(:last-child) {
            margin-inline-end: 40px;
        }
    }
}