.hero-image {
    background-image: url("https://images.unsplash.com/photo-1530789253388-582c481c54b0?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .hero-text {
        text-align: center;
        position: absolute;
        top: 35%;
        left: 50%;
        transform: translate(-50%, -50%);
        h1 {
            color: ivory;
            font-size: 3.5rem;
            margin-bottom: 1rem;
        }
        p {
            color: ivory;
            font-size: 2rem;
            margin-bottom: 1rem;
        }
        button {
            border:none;
            border-radius: 43rem;
            outline: 0;
            font-size: 1.5rem;
            display: inline-block;
            padding: 10px 25px;
            color: ivory;
            background-color: rgba(133, 89, 134, 0.808);
            text-align: center;
            cursor: pointer;
        }

        button:hover {
            background-image: linear-gradient(rgb(218, 3, 110), rgb(133, 89, 134));
        }
    }
}
